.form__submit {
  max-width: 358px;
  height: 50px;
  color: white;
  background-color: black;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  transition: opacity .2s ease-in-out;
}

.form__submit:hover {
  opacity: .8;
}

.form__submit:disabled {
  opacity: .2;
  cursor: default;
}